.submission-reply-modal {
  display: flex;
  flex-direction: column;
  height: 80vh;
  min-width: 900px;
  max-width: 1200px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
  border-bottom: 1px solid #e0e0e0;
  background: #f5f5f5;
  
  h2 {
    margin: 0;
    font-size: 1.3rem;
    font-weight: 600;
    color: #333;
  }
  
  .close-btn {
    color: #666;
  }
}

.loading-container,
.error-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  color: #666;
  
  mat-icon {
    font-size: 48px;
    width: 48px;
    height: 48px;
    color: #f44336;
  }
}

.modal-content {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.submission-panel {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  border-right: 1px solid #e0e0e0;
  background: #fafafa;
}

.submission-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  
  h3 {
    margin: 0;
    font-size: 1.1rem;
    color: #333;
  }
  
  .status-badge {
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 0.85rem;
    font-weight: 500;
    
    &.status-graded {
      background: #c8e6c9;
      color: #2e7d32;
    }
    
    &.status-submitted {
      background: #fff3e0;
      color: #ef6c00;
    }
    
    &.status-pending {
      background: #e0e0e0;
      color: #616161;
    }
  }
}

.submission-info {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 20px;
  padding: 12px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  
  .info-item {
    display: flex;
    gap: 8px;
    
    .label {
      color: #666;
      font-weight: 500;
    }
    
    .score-display {
      color: #1976d2;
      font-weight: 600;
    }
  }
}

.submitted-content,
.feedback-section {
  margin-bottom: 20px;
  
  h4 {
    margin: 0 0 12px 0;
    font-size: 1rem;
    color: #333;
  }
  
  .content-box,
  .feedback-box {
    padding: 16px;
    background: #fff;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    
    p {
      margin: 0;
      line-height: 1.6;
      white-space: pre-wrap;
    }
    
    a {
      color: #1976d2;
      display: flex;
      align-items: center;
      gap: 8px;
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
    }
    
    .no-content {
      color: #999;
      font-style: italic;
    }
  }
  
  .feedback-box {
    background: #fff8e1;
    border-color: #ffcc02;
  }
}

.reply-panel {
  width: 400px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  background: #fff;
  
  h4 {
    margin: 0;
    font-size: 1rem;
    color: #333;
    font-weight: 600;
  }
}

.grade-section {
  label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #555;
  }
  
  .score-input-wrapper {
    display: flex;
    align-items: center;
    gap: 8px;
    
    .score-input {
      width: 100px;
      padding: 10px 12px;
      font-size: 1.1rem;
      border: 2px solid #1976d2;
      border-radius: 8px;
      text-align: center;
      
      &:focus {
        outline: none;
        border-color: #0d47a1;
      }
    }
    
    .score-unit {
      font-size: 1rem;
      color: #666;
    }
  }
}

.feedback-input-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  
  label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #555;
  }
  
  .reply-textarea {
    flex: 1;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 0.95rem;
    resize: none;
    font-family: inherit;
    line-height: 1.5;
    
    &:focus {
      outline: none;
      border-color: #1976d2;
    }
    
    &::placeholder {
      color: #aaa;
    }
  }
}

.button-group {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  padding-top: 12px;
  border-top: 1px solid #e0e0e0;
  
  button {
    mat-icon {
      margin-right: 4px;
    }
  }
}
